.Input {
  &:valid {
    padding: 5px 6px 13px;
    margin-bottom: 24px;
    width: 100%;
    height: 47px;
    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    font-size: 14px;
    line-height: 17px;
    box-sizing: border-box;
  }
  &:invalid {
    padding: 5px 6px 13px;
    margin-bottom: 9px;
    width: 100%;
    height: 47px;
    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    font-size: 14px;
    line-height: 17px;
    box-sizing: border-box;
  }
}
